export default {
  data() {
    return {
      animationData1: {},
      animationData2: {},
      activeKey: "",
      inputValue: [],
    };
  },
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
    //是否为带小数的数字键盘
    decimals: {
      type: Boolean,
      default: true,
    },
    //小数点前整数的位数
    maxLength: {
      type: [Number, String],
      default: 9,
    },
    //小数点后的位数
    pointLength: {
      type: [Number, String],
      default: 2,
    },
    //最大的值
    max: {
      type: [Number, String],
      default: 999999999,
    },
    //最小的值
    min: {
      type: [Number, String],
      default: 0,
    },
  },
  computed: {
    keyboardList() {
      return this.decimals
        ? ["1", "2", "3", "4", "5", "6", "7", "8", "9", ".", "0", "删除"]
        : ["1", "2", "3", "4", "5", "6", "7", "8", "9", "", "0", "删除"];
    },
  },
  watch: {
    visible(val) {
      this.inputValue = [];
      if (val) this.enterAnimate();
      else this.leaveAnimate();
    },
  },
  methods: {
    setValue(data) {
      this.activeKey = data;
      let timer = setTimeout(() => {
        this.activeKey = "-";
        clearTimeout(timer);
      }, 100);

      if (
        !data ||
        (data !== "删除" &&
          this.inputValue.includes(".") &&
          this.inputValue.join("").split(".")[1].length === this.pointLength)
      )
        return false;

      if (
        this.inputValue.length === 1 &&
        this.inputValue[0] === "0" &&
        !["删除", "."].includes(data)
      ) {
        return (this.inputValue = [data]);
      }

      if (data !== "删除") {
        if (this.inputValue.includes(".")) {
          if (
            this.inputValue.join("").split(".")[0].length === this.maxLength
          ) {
            return false;
          }
        } else if (this.inputValue.length === this.maxLength) {
          return false;
        }
      }

      if (data === "删除") {
		// this.$emit("Delete")
        this.inputValue.length && this.inputValue.pop();
      }
      if (data === ".") {
        if (!this.inputValue.length || this.inputValue.includes("."))
          return false;
      }
	  this.$emit('SetValue', data)
      this.inputValue.push(data);
    },
    close() {
      this.$emit("update:visible", false);
      this.$emit("close");
    },
    confirm() {
      // let result = Number(this.inputValue.join(""));
      // if (result < this.min) {
      //   uni.showToast({
      //     title: `最小值为${this.min}`,
      //     duration: 1500,
      //     icon: "none",
      //   });
      //   return;
      // } else if (result > this.max) {
      //   uni.showToast({
      //     title: `最大值为${this.max}`,
      //     duration: 1500,
      //     icon: "none",
      //   });
      //   return;
      // }
      this.$emit("confirm");
      this.close();
    },
    enterAnimate() {
      this.animation1 = uni.createAnimation();
      this.animation2 = uni.createAnimation();
      this.animation1.backgroundColor("rgba(0,0,0,0.5)").step({
        duration: 500,
        timingFunction: "ease",
      });
      this.animation2.translateY(0).step({
        timingFunction: "ease-out",
        duration: 500,
      });
      this.animationData1 = this.animation1.export();
      this.animationData2 = this.animation2.export();
    },
    leaveAnimate() {
      this.animation1 = uni.createAnimation();
      this.animation2 = uni.createAnimation();
      this.animation1.backgroundColor("rgba(0,0,0,0)").step({
        duration: 500,
        timingFunction: "ease",
      });
      this.animation2.translateY(500).step({
        timingFunction: "ease-out",
        duration: 500,
      });
      this.animationData1 = this.animation1.export();
      this.animationData2 = this.animation2.export();
    },
  },
};
